<template>
  <div>
    <div v-if="loading" class="loading-overlay">
      <ActivityIndicator size="large" color="#000" />
    </div>
    <div class="container">
      <h3>Absensi Kehadiran</h3>
      <div class="container-box">
        <div class="slider-container">
          <div class="slider-button"
            :style="{ transform: `translateX(${slidePosition}px)`, backgroundColor: sliderColor }" @mousedown="startPan"
            @mousemove="handlePan" @mouseup="endPan">
            <span class="slider-icon"></span>
          </div>
          <div class="slider-background">
            <p class="slider-text">{{ labelSlider }}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="attendance-container-wrapper">
      <div class="attendance-container">
        <p class="attendance-title">Check Clock Terakhir:</p>
        <div v-for="(item, index) in attendance" :key="index" class="attendance-card">
          <p v-if="item?.clock_out_time" class="clock-out-text">
            Absen Keluar hari {{ getDayName(item.clock_out_time) }} jam {{ formatTime(item?.clock_out_time) }}
          </p>
          <p v-if="item?.clock_in_time" class="clock-in-text">
            Absen Masuk hari {{ getDayName(item.clock_in_time) }} jam {{ formatTime(item?.clock_in_time) }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import axios from '@/utils/axios';

export default {
  props: {
    location: Object,
  },
  data() {
    return {
      loading: false,
      isPanning: false,
      slidePosition: 0,
      maxSlideDistance: 250,
    }
  },
  computed: {
    ...mapState(['statistic']),
    attendance() {
      return this.statistic?.check_clock?.attendance || [];
    },
    isSliderEnabled() {
      return ['enableClockIn', 'enableClockOut'].includes(this.statistic?.check_clock?.status);
    },
    labelSlider() {
      if (this.statistic?.check_clock?.status === 'enableClockIn') {
        return 'Geser untuk Check Clock In';
      } else if (this.statistic?.check_clock?.status === 'enableClockOut') {
        return 'Geser untuk Check Clock Out';
      }
      return 'Tidak Tersedia';
    },
    sliderColor() {
      return this.isSliderEnabled ? 'green' : '#ccc';
    },
  },
  methods: {
    async handleSlideComplete() {
      try {
        const type = this.labelSlider?.includes('In') ? 'in' : 'out';
        const { latitude, longitude, loc } = this.location || {};
        const params = {
          type,
          latitude,
          longitude,
          location: loc?.display_name,
        };

        const response = await axios.post('/check-clock', params);

        if (response.data.success) {
          this.isSliderEnabled = false;
          const currentTime = new Date().toLocaleTimeString();
          this.labelSlider = `Sukses ${type === 'in' ? 'Clock In' : 'Clock Out'} pada jam ${currentTime}`;
          this.sliderColor = 'blue';
        } else {
          alert(response.data?.msg || 'Gagal melakukan clock!');
          this.resetSlider();
        }
      } catch (error) {
        alert('Gagal terhubung ke server');
        this.resetSlider();
      }
    },
    resetSlider() {
      this.slidePosition = 0;
    },

    startPan(event) {
      if (!this.isSliderEnabled) return;
      this.isPanning = true;
      event.preventDefault(); // Mencegah seleksi teks saat menggeser
    },
    handlePan(event) {
      if (!this.isPanning) return;
      const deltaX = event.movementX;
      if (this.slidePosition + deltaX >= 0 && this.slidePosition + deltaX <= this.maxSlideDistance) {
        this.slidePosition += deltaX;
      }
    },
    endPan() {
      this.isPanning = false;
      if (this.slidePosition >= this.maxSlideDistance) {
        this.isSliderEnabled = false;
        this.handleSlideComplete();
      } else {
        this.resetSlider();
      }
    },
    getDayName(dateString) {
      const days = ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu'];
      const date = new Date(dateString);
      return days[date.getDay()];
    },
    formatTime(timeString) {
      const date = new Date(timeString);
      return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    },
  },
  mounted() {
    // this.checkServerStatus();
  },
};
</script>


<style scoped>
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.container-box {
  padding: 20px;
}

.slider-container {
  width: 300px;
  height: 50px;
  border-radius: 25px;
  background-color: black;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
}

.slider-background {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider-text {
  color: #888;
  font-size: 12px;
}

.slider-button {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 1;
  cursor: grab;
}

.slider-icon {
  font-size: 24px;
  color: #fff;
}

.attendance-container {
  margin-top: 20px;
  padding: 20px;
}

.attendance-title {
  font-size: 13px;
  font-weight: bold;
  color: #333;
}

.attendance-card {
  background-color: #fff;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  border: 1px solid #ddd;
}

.clock-in-text {
  color: #4caf50;
  font-size: 11px;
  font-weight: 600;
}

.clock-out-text {
  color: #f44336;
  font-size: 11px;
  font-weight: 600;
}

.loading-overlay {
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
