<template>
    <div v-if="visible" class="overlay">
        <div class="kasbon-detail">
            <h2>Detail Kasbon {{ kasbon.ref_no }}</h2>
            <p><strong>Jumlah:</strong> {{ formatCurrency(kasbon.amount) }}</p>
            <p><strong>Angsuran per Bulan:</strong> {{ formatCurrency(kasbon.angsuran_perbulan) }}</p>
            <p><strong>Tenor:</strong> {{ kasbon.tenor }} bulan</p>
            <p><strong>Status:</strong> {{ kasbon.status }}</p>
            <p><strong>Keperluan:</strong> {{ kasbon.keperluan }}</p>
            <p><strong>Jaminan:</strong> {{ kasbon.jaminan || 'Tidak ada' }}</p>
            <p><strong>Kepemilikan Jaminan:</strong> {{ kasbon.owned_jaminan }}</p>
            <p><strong>Masa Berlaku Jaminan:</strong> {{ kasbon.expire_date_jaminan || 'Tidak ada' }}</p>
            <p><strong>Catatan:</strong> {{ kasbon.note || 'Tidak ada catatan' }}</p>

            <!-- Tampilkan gambar jika ada -->
            <div v-if="kasbon.documents && kasbon.documents.length" class="document-container">
                <h3>Dokumen</h3>
                <div v-for="(doc, index) in kasbon.documents" :key="index" class="document-wrapper">
                    <img :src="doc.uri" class="document-image" @click="showFullscreen(doc.uri)" />
                </div>
            </div>

            <!-- Tampilkan gambar dalam tampilan layar penuh jika diklik -->
            <div v-if="fullscreenImage" class="fullscreen-overlay" @click="fullscreenImage = null">
                <img :src="fullscreenImage" class="fullscreen-image" />
            </div>

            <button @click="$emit('close')" class="close-button">Tutup</button>
        </div>
    </div>
</template>

<script>
export default {
    props: ['kasbon', 'visible'],
    data() {
        return {
            fullscreenImage: null
        };
    },
    methods: {
        formatCurrency(value) {
            return new Intl.NumberFormat('id-ID', {
                style: 'currency',
                currency: 'IDR'
            }).format(value);
        },
        showFullscreen(uri) {
            this.fullscreenImage = uri;
        }
    }
};
</script>

<style scoped>
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.kasbon-detail {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    max-width: 600px;
    width: 100%;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.document-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 15px;
}

.document-image {
    width: 80px;
    height: 80px;
    border-radius: 5px;
    cursor: pointer;
}

.fullscreen-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1001;
}

.fullscreen-image {
    max-width: 90%;
    max-height: 90%;
}

.close-button {
    margin-top: 20px;
    padding: 10px 15px;
    background-color: #e74c3c;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
</style>
