<template>
    <div class="form-container">
      <h2 style="margin-bottom: 1em;">Pengajuan Peminjaman Alat/Aset</h2>
      <div class="form-group">
        <label for="asset-search">Pilih Daftar Asset</label>
        <input
        autocomplete="off"
          type="text"
          id="asset-search"
          v-model="searchQuery"
          placeholder="Cari Alat / Asset"
          @focus="showDropdown = true"
          @input="filterAssets"
          @blur="hideDropdown"
          class="form-input"
        />
        <div
          v-if="showDropdown && filteredAssets.length"
          class="dropdown"
          @mousedown.prevent
        >
          <div
            v-for="(asset, index) in filteredAssets"
            :key="index"
            @click="selectAsset(asset)"
            class="dropdown-item"
          >
            {{ asset.name }}
          </div>
        </div>
      </div>
  
      <div v-if="selectedAsset" class="selected-asset">
  <strong>Alat / Asset :</strong> {{ selectedAsset.name }}
</div>

  
      <div class="form-actions">
        <button type="button" @click="submitAsset" :disabled="!selectedAsset" class="btn-submit">
          Ajukan
        </button>
        <button type="button" @click="closeForm" class="btn-cancel">Cancel</button>
      </div>
    </div>
  </template>
  
  <script>
  import axios from "@/utils/axios";
  
  export default {
    data() {
      return {
        toolFromServer: [],
        filteredAssets: [],
        searchQuery: "",
        selectedAsset: null,
        showDropdown: false,
        loading: false,
      };
    },
    methods: {
      async fetchFormTooling() {
        try {
          const { data } = await axios.get("/asset/getAssetFormData");
          if (data && typeof data === "object") {
            this.toolFromServer = Object.entries(data).map(([key, name]) => ({
              key,
              name: name.split(" (")[0].trim(),
            }));
            this.filteredAssets = this.toolFromServer;
          }
        } catch (error) {
          console.error("Error fetching form data", error);
          this.$toast?.error("Error fetching form data. Please try again.");
        }
      },
      filterAssets() {
        this.filteredAssets = this.toolFromServer.filter(asset =>
          asset.name.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      },
      selectAsset(asset) {
        this.selectedAsset = asset;
        this.showDropdown = false;
        this.searchQuery = asset.name; // Set input to selected asset name
      },
      hideDropdown() {
        // Delay hiding to allow click event on dropdown items
        setTimeout(() => (this.showDropdown = false), 200);
      },
      async submitAsset() {
        this.loading = true;
        try {
          const response = await axios.post("/asset/post-asset-loan-form", { asset_id: this.selectedAsset.key });
          const { success, msg } = response.data;
          if (success) {
            this?.$toast.success(msg || "Data berhasil disimpan.");
            setTimeout(() => {
            this.$router.push({
              name: 'Tooling',
            });
          }, 2e3);
          
          } else {
            this.$toast?.error(msg || "Terjadi kesalahan. Silakan coba lagi.");
          }
        } catch (error) {
          console.error("Error submitting asset", error);
          this.$toast?.error("Error submitting asset. Please try again.");
        } finally {
          this.loading = false;
        }
      },
      closeForm() {
        this.$router.push({ name: "Tooling" });
      },
    },
    created() {
      this.fetchFormTooling();
    },
  };
  </script>
  
  <style scoped>
  .form-container {
    padding: 20px;
    background-color: #fff;
  }
  .form-group {
    margin-bottom: 15px;
  }
  .form-input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  .dropdown {
    max-height: 150px;
    overflow-y: scroll;
    border: 1px solid #ccc;
    background-color: #fff;
    position: absolute;
    width: 100%;
    z-index: 1000;
  }
  .dropdown-item {
    padding: 10px;
    cursor: pointer;
  }
  .dropdown-item:hover {
    background-color: #f0f0f0;
  }
  .selected-asset {
  margin-top: 10px;
  padding: 15px;
  background-color: #e0f0ff; /* Light blue background */
  border: 1px solid #0044cc; /* Blue border */
  border-radius: 8px;
  font-size: 16px;
  color: #0044cc; /* Blue text color */
  text-align: center;
  font-weight: bold;
  margin-bottom: 5em;
}

  .form-actions {
    display: flex;
    justify-content: flex-end;
  }
  .btn-submit,
  .btn-cancel {
    padding: 10px 15px;
    margin-left: 10px;
  }
  .btn-submit {
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  .btn-cancel {
    background-color: #f44336;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  </style>
  