<template>
    <div>
        <div class="header-container">
            <h1 class="title">Daftar Alat / Asset</h1>
            <v-btn icon @click="navigateToAddTool" color="blue">
                <v-icon>mdi-plus-circle</v-icon>
            </v-btn>
        </div>

        <div class="container-hz" v-if="pendingRequestTool.length">
            <div style="height: 150px; overflow-x: scroll;">
                <div class="header-container-hz">
                    <div v-for="(item, index) in pendingRequestTool" :key="index" class="item-container-hz pending"
                        @click="handleItemPress(item)">
                        <div class="row">
                            <span class="item-text">{{ item.asset_name.name }} (Pending)</span>
                        </div>
                        <div class="row">
                            <span class="item-label">No Ref :</span>
                            <span class="item-value">{{ item.no_ref }}</span>
                        </div>
                        <div class="row">
                            <span class="item-label">Tanggal :</span>
                            <span class="item-value">{{ item.trx_date }}</span>
                        </div>
                        <div class="row">
                            <span class="item-label">Person :</span>
                            <span class="item-value">{{ item.person.nickname }}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="container" v-if="toolingData.length">
            <div v-for="(item, index) in toolingData" :key="index" class="item-container"
                @click="handleItemPress(item)">
                <div class="row">
                    <span class="item-label">Nama Asset:</span>
                    <span class="item-value">{{ item.asset }}</span>
                </div>
                <div class="row">
                    <span class="item-label">No Ref:</span>
                    <span class="item-value">{{ item.ref_no }}</span>
                </div>
                <div class="row">
                    <span class="item-label">Dialokasikan Dari:</span>
                    <span class="item-value">{{ item.allocated_at }}</span>
                </div>
            </div>
        </div>

        <div v-if="detailVisible" class="modal-overlay" @click.self="detailVisible = false">
            <div class="modal-content">
                <h2>Detail Asset</h2>
                <div v-if="selectedAsset">
                    <div class="row" v-for="(value, key) in assetDetails" :key="key">
                        <span class="item-label">{{ key }}:</span>
                        <span class="item-value">{{ value }}</span>
                    </div>
                </div>
                <button @click="detailVisible = false" class="close-button">Tutup</button>

            </div>
        </div>

    </div>
</template>

<script>
import axios from "@/utils/axios";

export default {

    data() {
        return {
            toolingData: [],
            pendingRequestTool: [],
            detailVisible: false,
            selectedAsset: null,
        };
    },
    computed: {
        assetDetails() {
            if (!this.selectedAsset) return {};
            if (this.selectedAsset?.status == 'pending')
                return {
                    'No ref': this.selectedAsset?.no_ref,
                    'Nama Asset': this.selectedAsset?.asset_name?.name,
                    'Person': this.selectedAsset?.person?.nickname,
                    'Jumlah': this.selectedAsset?.qty,
                    Status: 'Pending'
                }
            return {
                "No Ref": this.selectedAsset.ref_no,
                "Nama Asset": this.selectedAsset.asset,
                Model: this.selectedAsset.model,
                Jumlah: this.selectedAsset.quantity,
                "Admin Asset": this.selectedAsset.provider_name,
                Penerima: this.selectedAsset.receiver_name,
                "Dialokasikan Dari": this.selectedAsset.allocated_at,
                "Dialokasikan Hingga": this.selectedAsset.allocated_upto,
            };
        },
    },
    methods: {
        async fetchTooling() {
            try {
                const response = await axios.get("/asset/asset-list");
                this.toolingData = response.data.assets || [];
                this.pendingRequestTool = response.data.asset_loans || [];
            } catch (error) {
                console.error("Error fetching tooling data", error);
            }
        },
        handleItemPress(item) {
            this.selectedAsset = item;
            this.detailVisible = true;
        },
        navigateToAddTool() {
            this.$router.push({ name: "ToolingForm" });
        },
    },
    created() {
        this.fetchTooling();
    },
};
</script>

<style scoped>
.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.container-hz {
    overflow-x: auto;
    /* Membuat scroll horizontal */
    white-space: nowrap;
    /* Mencegah item turun ke baris berikutnya */
    margin-bottom: 2em;
}

.header-container-hz {
    display: flex;
    /* Menyusun item secara horizontal */
}

.item-container-hz {
    width: 75vw;
    margin-right: 15px;
    padding: 15px;
    border-radius: 10px;
}

.item-container-hz.pending {
    background-color: yellow;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 10px;
}

.container {
    padding: 10px;
}

.item-container {
    border-radius: 10px;
    padding: 15px;
    background-color: #56db3e;
    margin-bottom: 10px;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    /* Warna latar belakang transparan */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    /* Pastikan di atas elemen lain */
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    max-width: 500px;
    width: 100%;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

.item-label {
    text-align: left;
    /* Label rata kiri */
    flex: 1;
}

.item-value {
    text-align: right;
    /* Value rata kanan */
    flex: 1;
}

.item-text {
    font-weight: bold;
}
.close-button {
    background-color: #ff4d4d; /* Warna merah muda */
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  }

  .close-button:hover {
    background-color: #ff3333; /* Warna lebih gelap saat hover */
    transform: scale(1.05); /* Membesar sedikit saat hover */
  }

  .close-button:active {
    background-color: #e60000; /* Warna lebih gelap saat diklik */
    transform: scale(0.98); /* Efek sedikit mengecil saat diklik */
  }
</style>