<template>
    <div class="container">
      <h2 class="header">Daftar Kasbon</h2>
  
      <div v-if="kasbonList.length === 0" class="empty">
        Tidak ada data kasbon.
      </div>
  
      <KasbonItem v-for="kasbon in kasbonList"
            :key="kasbon.id"
            :kasbon="kasbon"
            @press="showKasbonDetail" />
  
            <KasbonDetail :kasbon="selectedKasbon" :visible="modalVisible" @close="closeModal"/>

      <button class="add-button" @click="() => visibleForm = true">
        Tambah Kasbon Baru
      </button>
      <KasbonForm v-if="visibleForm" :visible="visibleForm" @close="visibleForm = false" @refreshData="fetchKasbonList" />
  
    </div>
  </template>
  
  <script>
  import axios from '@/utils/axios';
  import KasbonItem from './KasbonItem.vue';
  import KasbonDetail from './KasbonDetail.vue';
import KasbonForm from './KasbonForm.vue';
  
  export default {
    components: {
        KasbonForm,
      KasbonItem,
      KasbonDetail,
    },
    data() {
      return {
        kasbonList: [],
        selectedKasbon: null,
        loading: false,
        modalVisible: false,
        visibleForm: false
      };
    },
    methods: {
      async fetchKasbonList() {
        this.loading = true;
        try {
          const response = await axios.get('/kasbon/get-kasbon-list');
          this.kasbonList = response.data;
        } catch (error) {
          console.error('Error fetching kasbon list:', error);
        } finally {
          this.loading = false;
        }
      },
      showKasbonDetail(kasbon) {
            this.selectedKasbon = kasbon;
            this.modalVisible = true;
        },
      closeModal() {
        this.modalVisible = false;
        this.selectedKasbon = null;
      },
    },
    mounted() {
      this.fetchKasbonList();
    },
  };
  </script>
  
  

<style scoped>
.container {
    padding: 16px;
    background-color: #f4f4f4;
}

.header {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 16px;
}

.add-button {
    background-color: #3498db;
    padding: 16px;
    border-radius: 8px;
    color: #fff;
    font-weight: bold;
    font-size: 16px;
    text-align: center;
    cursor: pointer;
}

.loading,
.empty {
    text-align: center;
    margin: 20px 0;
}
</style>