<template>
    <div v-if="visible" class="modal-overlay">
        <div class="modal-content">
            <h2>Form Kasbon</h2>
            <div class="form-container">
                <!-- Field Amount -->
                <label class="label">Jumlah: <span class="required">*</span></label>
                <input v-model="amount" type="number" placeholder="Jumlah Pengajuan" class="input"
                    @input="calculateAngsuranPerBulan" />

                <!-- Field Keperluan -->
                <label class="label">Keperluan: <span class="required">*</span></label>
                <input v-model="keperluan" type="text" placeholder="Keperluan" class="input" />

                <!-- Field Tenor -->
                <label class="label">Tenor (1 - 24 bulan) <span class="required">*</span></label>
                <select v-model="tenor" class="input" @change="calculateAngsuranPerBulan">
                    <option v-for="n in 24" :key="n" :value="n">{{ n }} bulan</option>
                </select>

                <!-- Field Angsuran Per Bulan -->
                <label class="label">Angsuran Per Bulan</label>
                <input v-model="angsuranPerBulan" type="text" placeholder="Angsuran Per Bulan" class="input" readonly />

                <!-- Field Jaminan -->
                <label class="label">Jaminan</label>
                <input v-model="jaminan" type="text" placeholder="Jaminan yang diberikan" class="input" />

                <!-- Field Expire Date Jaminan -->
                <label class="label">Masa berlaku jaminan</label>
                <input v-model="expireDateJaminan" type="date" placeholder="Masa berlaku" class="input" />

                <!-- Field Owned Jaminan -->
                <label class="label">Kepemilikan jaminan</label>
                <button @click="toggleOwnedJaminan" class="input">{{ ownedJaminan }}</button>

                <!-- Field Note -->
                <label class="label">Catatan</label>
                <textarea v-model="note" placeholder="Catatan" class="input"></textarea>

                <!-- Upload Documents -->
                <div class="document-container">
                    <input type="file" @change="openDocumentPicker" accept="image/*" hidden ref="fileInput" multiple />
                    <button @click="selectImage" class="add-button">
                        <v-icon left>mdi-paperclip</v-icon>
                    </button>

                    <div v-for="(doc, index) in documents" :key="index" class="document-wrapper">
                        <img :src="doc.uri" class="document-image" @click="showFullscreen(doc.uri)" />
                        <button @click="removeDocument(index)" class="remove-button"></button>
                    </div>
                </div>

                <!-- Fullscreen Image Preview -->
                <div v-if="fullscreenImage" class="fullscreen-overlay" @click="fullscreenImage = null">
                    <img :src="fullscreenImage" class="fullscreen-image" />
                </div>

                <!-- Button Submit and Close -->
                <div class="button-container">
                    <button @click="onClose" class="close-button">Tutup</button>
                    <button @click="handleSubmit" class="submit-button" :disabled="loading">
                        {{ loading ? "Mengirim..." : "Submit" }}
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from '@/utils/axios';

export default {
    props: ['visible'],
    emits: ['close', 'refreshData'],
    data() {
        return {
            amount: '',
            keperluan: '',
            tenor: 1,
            angsuranPerBulan: '',
            jaminan: '',
            expireDateJaminan: '',
            ownedJaminan: 'milik sendiri',
            note: '',
            documents: [],
            loading: false,
            isVisible: this.visible,
            fullscreenImage: null,
        };
    },
    watch: {
        visible(newVal) {
            this.isVisible = newVal; // Update local visibility when prop changes
        }
    },
    methods: {
        selectImage() {
            this.$refs.fileInput.click(); // Click input file programmatically
        },
        onClose() {
            this.isVisible = false; // Change local visibility
            this.$emit('close'); // Emit close event to parent
        },
        openDocumentPicker(event) {
            const files = event.target.files;
            for (let i = 0; i < files.length; i++) {
                const file = files[i];
                const reader = new FileReader();
                reader.onload = (e) => {
                    this.documents.push({ uri: e.target.result }); // Store image URI
                };
                reader.readAsDataURL(file); // Read image file as base64
            }
        },
        toggleOwnedJaminan() {
            this.ownedJaminan = this.ownedJaminan === 'milik sendiri' ? 'bukan hak milik' : 'milik sendiri';
        },
        removeDocument(index) {
            this.documents.splice(index, 1);
        },
        async handleSubmit() {
            if (!this.amount || !this.keperluan || !this.tenor) {
                alert('Semua field yang wajib diisi harus diisi.');
                return;
            }

            this.loading = true;
            const formData = new FormData();
            formData.append('amount', this.amount);
            formData.append('keperluan', this.keperluan);
            formData.append('tenor', this.tenor);
            formData.append('angsuran_per_bulan', this.angsuranPerBulan);
            formData.append('jaminan', this.jaminan);
            formData.append('expire_date_jaminan', this.expireDateJaminan);
            formData.append('owned_jaminan', this.ownedJaminan);
            formData.append('note', this.note);

            this.documents.forEach((doc) => {
                formData.append('documents[]', doc);
            });

            try {
                const response = await axios.post('/kasbon/post-kasbon-form-data', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    },
                });
                if (response.status === 200) {
                    this.resetForm();
                    this.$emit('refreshData');
                    this.$toast?.success('Data berhasil diajukan.');
                    this.$emit('close');
                }
            } catch (error) {
                this.$toast?.error('Gagal mengirim data. Silakan coba lagi.');
            } finally {
                this.loading = false;
            }
        },
        showFullscreen(uri) {
            this.fullscreenImage = uri; // Set the URI for the fullscreen image
        },
        resetForm() {
            this.amount = '';
            this.keperluan = '';
            this.tenor = 1;
            this.angsuranPerBulan = '';
            this.jaminan = '';
            this.expireDateJaminan = '';
            this.ownedJaminan = 'milik sendiri';
            this.note = '';
            this.documents = [];
        },
        formatAmount() {
            const numberValue = parseFloat(this.amount.replace(/[^\d]/g, ''));
            if (!isNaN(numberValue)) {
                this.amount = new Intl.NumberFormat('id-ID', {
                    style: 'currency',
                    currency: 'IDR',
                }).format(numberValue);
            }
            this.calculateAngsuranPerBulan();
        },
        calculateAngsuranPerBulan() {
            const numberValue = parseFloat(this.amount.replace(/[^\d]/g, ''));
            if (!isNaN(numberValue) && this.tenor) {
                const angsuran = numberValue / this.tenor;
                this.angsuranPerBulan = new Intl.NumberFormat('id-ID', {
                    style: 'currency',
                    currency: 'IDR',
                }).format(angsuran);
            } else {
                this.angsuranPerBulan = '';
            }
        },
    },
};
</script>

<style scoped>
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    max-width: 600px;
    width: 100%;
}

.label {
    font-size: 16px;
    margin-bottom: 8px;
}

.required {
    color: red;
}

.input {
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 16px;
    width: 100%;
}

.document-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}



.button-container {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.close-button,
.submit-button {
    padding: 12px;
    border-radius: 8px;
    font-weight: bold;
    color: #fff;
    width: 48%;
}

.close-button {
    background-color: #e74c3c;
}

.submit-button {
    background-color: #3498db;
}

.document-image {
    width: 60px;
    height: 60px;
    border-radius: 5px;
    cursor: pointer;
}

fullscreen-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.9);
    /* Dark background for fullscreen */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    /* Ensure it is on top */
}

.fullscreen-image {
    max-width: 100%;
    max-height: 100%;
}
</style>